html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
}

::-webkit-file-upload-button {
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-SemiBold.woff2") format("woff2"), url("../fonts/Lato-SemiBold.woff") format("woff");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Bold.woff2") format("woff2"), url("../fonts/Lato-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-ExtraBold.woff2") format("woff2"), url("../fonts/Lato-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  object-fit: cover;
}

body {
  min-width: 320px;
  font-family: "Lato", sans-serif;
}
body::-webkit-scrollbar {
  width: 7px;
  background: #3D4050;
}
body::-webkit-scrollbar-thumb {
  width: 7px;
  background: #3E9CDC;
  border-radius: 10px;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1232px;
}

.stop-scroll {
  overflow: hidden;
}

.logo__icon {
  width: inherit;
  height: inherit;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12.5px;
  width: 100%;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.2em;
  text-transform: uppercase;
  color: #FFF;
  background: #3E9CDC;
  border: 1px solid #FFF;
  border-radius: 4px;
  cursor: pointer;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
}
.button:hover {
  color: #3E9CDC;
  background-color: #FFF;
  border-color: #3E9CDC;
}

.stages {
  overflow-x: auto;
}
.stages__wrapper {
  display: flex;
  justify-content: space-between;
  min-width: 1170px;
}
.stages__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-bottom: 5px;
  width: 209px;
}
.stages__circle {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  background-color: #3E9CDC;
  border-radius: 50%;
  border: 5px solid #286690;
}
.stages__circle-icon {
  display: block;
  max-width: 50px;
  max-height: 50px;
}
.stages__circle-icon_search {
  width: 34px;
  height: 34px;
}
.stages__circle-icon_percent {
  width: 31px;
  height: 31px;
}
.stages__circle-icon_file {
  width: 39px;
  height: 42px;
}
.stages__circle-icon_mail {
  width: 36px;
  height: 29px;
}
.stages__circle-icon_money {
  width: 39px;
  height: 26px;
}
.stages__text {
  width: 188px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #FFF;
}
.stages__dots-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: absolute;
  left: 110px;
  display: flex;
  gap: 20px;
}
.stages__dots-item {
  width: 6px;
  height: 6px;
  background: #C4C4C4;
  border-radius: 50%;
}
.stages::-webkit-scrollbar {
  height: 7px;
}
.stages::-webkit-scrollbar-track {
  background: #3D4050;
  border: 0;
  border-radius: 15px;
}
.stages::-webkit-scrollbar-thumb {
  background-color: #3E9CDC;
  border-radius: 15px;
}

.form {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "select-menu email name" "slider slider file" ". button .";
  gap: 30px;
  width: 100%;
}
.form__input {
  padding: 12px 10px;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  background: #DFDFE0;
  border: 1px solid #DFDFE0;
  border-radius: 3px;
  color: #272733;
  outline: none;
}
.form__input::placeholder {
  color: inherit;
}
.form__input_email {
  grid-area: email;
}
.form__input_name {
  grid-area: name;
}
.form__input_file {
  grid-area: file;
}
.form__file-input {
  display: none;
}
.form__file-label {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  height: 48px;
  color: #272733;
  background: #FFF;
  border: 2px solid #FFF;
  border-radius: 3px;
  transition: border-color 0.15s ease-in-out;
}
.form__file-label:hover {
  border-color: #3E9CDC;
  cursor: pointer;
}
.form__file-icon {
  width: 17px;
}
.form__file-text {
  font-size: 15px;
  font-weight: 600;
  line-height: 18px;
  text-transform: uppercase;
}
.form__button {
  grid-area: button;
  padding: 14px;
  font-size: 15px;
}

.select-menu {
  grid-area: select-menu;
  position: relative;
  display: flex;
  cursor: pointer;
}
.select-menu__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 10px;
  width: 100%;
  color: #272733;
  background: rgba(255, 255, 255, 0.85);
  border: 1px solid #FFF;
  border-radius: 3px;
}
.select-menu__arrow-icon {
  width: 20px;
  height: 5.2px;
  fill: #000;
  transition: transform 0.15s ease-in-out, fill 0.15s ease-in-out;
}
.select-menu__title {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2em;
  user-select: none;
  transition: color 0.15s ease-in-out;
}
.select-menu__list {
  position: absolute;
  width: 100%;
  height: 190px;
  bottom: -208px;
  display: none;
  gap: 10px;
  flex-direction: column;
  background-color: #272733;
  border: 1px solid #3D4050;
  border-radius: 3px;
  padding: 6px 10px 10px 10px;
  list-style-type: none;
  box-sizing: border-box;
  overflow-y: scroll;
  color: #FFF;
  font-size: 18px;
}
.select-menu__list::-webkit-scrollbar {
  width: 7px;
  background: #3D4050;
  border-radius: 3px;
}
.select-menu__list::-webkit-scrollbar-thumb {
  width: 7px;
  height: 53px;
  background: #3E9CDC;
  border-radius: 3px;
}
.select-menu__list.active {
  display: block;
}
.select-menu__item {
  display: block;
  padding: 7px 0;
  font-weight: 400;
  font-size: 17px;
  color: #FFF;
  user-select: none;
  transition: color 0.15s ease-in-out;
}
.select-menu__item:hover {
  color: #42A9ED;
  cursor: pointer;
}
.select-menu.active .select-menu__header {
  background: #3D4050;
  border-radius: 3px 3px 0px 0px;
  border: 1px solid #3D4050;
  color: #FFF;
}
.select-menu.active .select-menu__arrow-icon {
  transform: rotate(180deg);
  fill: #FFF;
}

.range-slider {
  grid-area: slider;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 30px;
  width: 100%;
  height: 48px;
}
.range-slider__header {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  font-size: 18px;
  line-height: 1.2em;
  color: #FFF;
}
.range-slider__value {
  opacity: 85%;
}
.range-slider__input {
  -webkit-appearance: none;
  outline: none;
  height: 6px;
  background: rgba(255, 255, 255, 0.7);
  border: 1px solid #FFF;
  border-radius: 5px;
  cursor: pointer;
}
.range-slider__input::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 27px;
  width: 27px;
  border-radius: 50%;
  background: #42A9ED;
  border: 3px solid #286690;
}

.error {
  position: absolute;
  top: 0;
  padding: 3px 11px;
  font-size: 10px;
  font-weight: 400;
  color: crimson;
}

.header {
  position: fixed;
  padding: 33.5px 0 31.5px;
  width: 100%;
  background-color: rgba(16, 16, 29, 0.9);
  transition: padding 0.15s ease-in-out;
  z-index: 1000;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  width: 247px;
  height: 35px;
}
.header__nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  gap: 62px;
}
.header__nav-link {
  position: relative;
  font-size: 13px;
  line-height: 1.2em;
  text-transform: uppercase;
  color: #FFF;
  transition: color;
}
.header__nav-link_active {
  color: #43ABF0;
}
.header__nav-link::after {
  content: "";
  position: absolute;
  bottom: -7px;
  left: 0;
  display: block;
  width: 0;
  height: 2px;
  background-color: #3E9CDC;
  transition: width 0.15s ease-in-out;
}
.header__nav-link:focus-visible {
  outline-offset: 5px;
  outline: 2px solid #3E9CDC;
}
.header__nav-link:hover::after {
  width: 100%;
}
.header__nav-link:active {
  color: #43ABF0;
}
.header.sticky {
  padding: 10px;
}

.intro {
  background: url(../img/main-bg.jpg) no-repeat center/cover;
  line-height: 1.2em;
}
.intro__wrapper {
  display: flex;
  flex-direction: column;
  gap: 85px;
  padding: 203px 0 202px;
}
.intro__title {
  margin: 0;
  max-width: 780px;
  font-size: 48px;
  font-weight: 700;
  color: #000;
  line-height: 1.2em;
  text-transform: uppercase;
}
.intro__title_blue {
  color: #3E9CDC;
}
.intro__content {
  font-size: 20px;
  line-height: 1.2em;
}
.intro__content-summary {
  display: block;
  margin-bottom: 10px;
  font-weight: 700;
}
.intro__content-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.intro__content-item {
  display: flex;
  align-items: center;
}
.intro__content-item::before {
  content: "";
  display: block;
  margin: 10px;
  width: 5px;
  height: 2px;
  background: #000;
}
.intro__content-buttons-wrap {
  display: flex;
  gap: 30px;
}
.intro__content-button {
  max-width: 183px;
}
.intro__content-button_white {
  color: #2A2B30;
  background: #FFF;
  border-color: #6F6D7B;
}
.intro__content-button_white:hover {
  color: #FFF;
  background: #6F6D7B;
  border-color: #6F6D7B;
}

.order {
  background: #272733 url(../img/order-bg.png) no-repeat center/cover;
}
.order__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 80px;
  padding: 120px 0 150px 0;
}
.order__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
.order__description-title {
  margin: 0;
  font-size: 36px;
  font-weight: 800;
  line-height: 1.2em;
  text-transform: uppercase;
  color: #FFF;
}
.order__description-title_blue {
  color: #3E9CDC;
}
.order__description-summary {
  font-size: 18px;
  line-height: 1.2em;
  color: #BBB;
}